const React = require('react')
const ReactDOM = require('react-dom')

class CheckBox extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      checked: false
    }
    // this.clickeHandler = this.clickeHandler.bind(this)
  }

  clickeHandler () {
    const newValue = !(this.state.checked)
    this.setState({
      checked: newValue
    })
  }

  render () {
    const mark = 'â– '
    let style = {
      color: '#ffffff'
    }
    if (this.state.checked) {
      style = {
        color: '#ffff00'
      }
    } else {
      style = {
        color: '#000000'
      }
    }
    return (<div onClick={() => { return this.clickeHandler() }}>
      <span style={style}> {mark} </span> {this.props.label}
    </div>)
  }
}

const dom = <div>
  <CheckBox label='Apple' />
  <CheckBox label='Banna' />
  <CheckBox label='Orange' />
  <CheckBox label='Mango' />
</div>

ReactDOM.render(dom, document.querySelector('#content'))
